import { database } from '../../config/firebase.config';
import { serverStatusType } from '../../redux/server/serverSlice';
import Swal from 'sweetalert2';

const changeFirebaseCancelCabinetUser = (
  cabinetNum: number,
  index: number,
  uuid: string | undefined | null,
) => {
  const postData = {
    cabinetIdx: null,
    cabinetTitle: null,
  };

  database.ref(`/cabinet/${cabinetNum}/item/${index}`).transaction(
    (cabinet) => {
      if (cabinet.uuid === uuid) {
        return { status: 0 };
      }

      return;
    },
    (error, committed) => {
      if (error) {
        Swal.fire({
          icon: 'error',
          title: '  ',
          text: `  .`,
          showConfirmButton: true,
          width: 'auto',
          timer: 2500,
        });
      } else if (!committed) {
        Swal.fire({
          icon: 'error',
          title: '  ',
          text: ` .`,
          showConfirmButton: true,
          width: 'auto',
          timer: 2500,
        });
      } else {
        Swal.fire({
          icon: 'success',
          title: '  ',
          width: 'auto',
          showConfirmButton: true,
          timer: 2000,
        });

        database.ref(`/users/${uuid}`).update(postData);
      }
    },
  );
};

export default changeFirebaseCancelCabinetUser;
