import { database } from '../../config/firebase.config';
import Swal from 'sweetalert2';

const changeCabinetStatus = (
  cabinetIndex: number,
  selectedCabinet: number,
  changeStatus: 0 | 2,
) => {
  if (changeStatus === 2) {
    Swal.fire({
      icon: 'warning',
      title: '  ',
      text: `    ?`,
      showCancelButton: true,
      showConfirmButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
      confirmButtonColor: 'rgb(63,81,181)',
    }).then((result) => {
      if (result.isConfirmed) {
        database.ref(`cabinet/${cabinetIndex}/item/${selectedCabinet}`).set({
          status: changeStatus,
        });

        Swal.fire({
          icon: 'success',
          title: '  ',
          width: 'auto',
          showConfirmButton: true,
          timer: 2000,
        });
      }
    });
  } else if (changeStatus === 0) {
    Swal.fire({
      icon: 'warning',
      title: '  ',
      text: `  ?`,
      showCancelButton: true,
      showConfirmButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
      confirmButtonColor: 'rgb(63,81,181)',
    }).then((result) => {
      if (result.isConfirmed) {
        database.ref(`cabinet/${cabinetIndex}/item/${selectedCabinet}`).set({
          status: changeStatus,
        });

        Swal.fire({
          icon: 'success',
          title: '  ',
          width: 'auto',
          showConfirmButton: true,
          timer: 2000,
        });
      }
    });
  }
};

export default changeCabinetStatus;
